}
@media (max-width: 767px) {
  .process__list {
    gap: 20px;
  }
}
@media (min-width: 768px) {
  .process__list {
    grid-template-columns: 1fr 1fr;
    gap: 37px 0;
    padding: 0 79px 0 0;
  }
}
