  }
}
.process__item {
  position: relative;
  display: grid;
  color: #444444;
}
@media (max-width: 767px) {
  .process__item {
    grid-template-columns: 60px 1fr;
    gap: 20px;
  }
}
@media (min-width: 768px) {
  .process__item {
    padding: 40px 0 57px 0;
    gap: 31px;
    grid-template-rows: min-content min-content;
    font-size: 16px;
    line-height: 24px;
  }
}
.process__item::before {
  content: "";
  display: block;
  position: static;
  width: 60px;
  height: 60px;
}
@media (min-width: 768px) {
  .process__item::before {
    width: 80px;
    height: 80px;
  }
}
.process__item::after {
  content: "";
  display: block;
  position: absolute;
}
@media (min-width: 768px) {
  .process__item::after {
    z-index: -1;
    right: 0;
    height: 280px;
    color: #f2f2f2;
    font-size: 280px;
    font-family: "Oswald", "Arial", sans-serif;
    line-height: 280px;
  }
}
@media (min-width: 768px) {
  .process__item--healthful {
    width: 238px;
