}
@media (min-width: 1300px) {
  .process__item--healthful {
    margin-right: 88px;
  }
}
.process__item--healthful::before {
  background: url("../img/Index/leaf.svg") no-repeat center;
}
@media (min-width: 768px) {
  .process__item--healthful::before {
    background: url("../img/Index/leaf-large.svg") no-repeat center;
  }
}
@media (min-width: 768px) {
  .process__item--healthful::after {
    content: "1";
    width: 112px;
  }
}
@media (min-width: 768px) {
